<template>
    <div ref="rect" class="d3-rect-2">
        <Layout title="" unit="" :ch="ch" :active="active">
            <div class="svg"></div>
        </Layout>
    </div>
</template>

<script>
import * as d3 from 'd3';
import Layout from './layout.vue'
import { linearGradient } from './utils'
export default {
    props: {
        active: {
            type: Boolean,
            default: false
        },
        eh: {
            type: Number,
            default: 100
        },
        inDelay: {
            type: Number,
            default: 0
        },
        outDelay: {
            type: Number,
            default: 0
        }
    },
    components: {
        Layout
    },
    computed: {
        ch() {
            return this.eh - 56
        }
    },
    mounted() {
        this.init()
    },
    methods: {
        init() {
            // 
            // const width = 480, height = this.ch;
            const data = [
                { category: "2021", apples: 30, oranges: 20, bananas: 25, color: ['rgb(73% 38% 64%)', 'rgb(73% 61% 64%)'] },
                { category: "2022", apples: 40, oranges: 25, bananas: 30, color: ['rgb(13% 41% 89%)', 'rgb(13% 66% 89%)'] },
                { category: "2023", apples: 35, oranges: 30, bananas: 28, color: ['rgb(60% 12% 26%)', 'rgb(100% 28% 48%)'] }
            ];

            // 
            // 
            const margin = { top: 40, right: 20, bottom: 50, left: 50 };
            const width = 480 - margin.left - margin.right;
            const height = this.ch - margin.top - margin.bottom;
            const barPadding = 0.2;

            // 
            const svg = d3
                .select(".d3-rect-2 .svg")
                .append("svg")
                .attr("width", width + margin.left + margin.right)
                .attr("height", height + margin.top + margin.bottom)
                .append("g")
                .attr("transform", `translate(${margin.left}, ${margin.top})`);

            // 
            const color = d3
                .scaleOrdinal()
                .domain(["apples", "oranges", "bananas"])
                .range(["#ff7f0e", "#2ca02c", "#1f77b4"]);

            // 
            const x0 = d3
                .scaleBand()
                .domain(data.map(d => d.category)) // 
                .range([0, width])
                .padding(barPadding);

            const x1 = d3
                .scaleBand()
                .domain(["apples", "oranges", "bananas"]) // 
                .range([0, x0.bandwidth()])
                .padding(0.05);

            const y = d3
                .scaleLinear()
                .domain([0, d3.max(data, d => Math.max(d.apples, d.oranges, d.bananas))])
                .nice()
                .range([height, 0]);

            //  X 
            svg.append("g")
                .attr('class', 'x-axis')
                .attr("transform", `translate(0,${height + 5})`)
                .call(d3.axisBottom(x0).tickFormat((a) => a).tickSize(0))
                .attr("font-size", '12px')
                .attr('color', '#fff')
                .attr("opacity", 0)
                .transition()
                .duration(1000) //  1000ms
                .attr("opacity", 1)

            svg.select(".x-axis")
                .selectAll(".domain")
                .style("display", "none");

            svg.select('.x-axis')
                .selectAll('text')
                .attr("transform", `translate(0,5)`)

            //  Y 

            svg.append("g")
                .attr('class', 'y-axis')
                .attr("transform", `translate(-5,0)`)
                .call(d3.axisLeft(y).ticks(5).tickSize(0))
                .attr("font-size", '12px')
                .attr('color', '#fff')
                .attr("opacity", 0)
                .transition()
                .duration(1000) //  1000ms
                .attr("opacity", 1)


            svg.select(".y-axis")
                .selectAll(".domain")
                .style("display", "none");

            // 
            // 
            data.map((el, i) => {
                const id = `d3-rect2-${i}`
                linearGradient(svg, id, el.color)
            })

            svg
                .selectAll(".barGroup")
                .data(data)
                .enter()
                .append("g")
                .attr("class", "barGroup")
                .attr("transform", d => `translate(${x0(d.category)}, 0)`)
                .selectAll("rect")
                .data(d =>
                    ["apples", "oranges", "bananas"].map(key => ({
                        key,
                        value: d[key]
                    }))
                )
                .enter()
                .append("rect")
                .attr("class", "bar")
                .attr("x", d => x1(d.key))
                .attr("y", d => y(0))
                .attr("width", x1.bandwidth())
                .attr("height", 0)
                .attr("fill", (d, i) => `url(#d3-rect2-${i})`)
                .transition()
                .duration(600) //  1000ms
                .delay((d, i) => i * 600 / data.length)
                .attr("y", d => y(d.value))
                .attr("height", d => height - y(d.value))

            // 
            const legend = svg
                .selectAll(".legend")
                .data(["", "", ""])
                .enter()
                .append("g")
                .attr("class", "legend")
                .attr("transform", (d, i) => `translate(${i * 100}, -30)`);

            legend
                .append("rect")
                .attr("x", 0)
                .attr("width", 15)
                .attr("height", 15)
                .attr("fill", (d, i) => `url(#d3-rect2-${i})`);

            legend
                .append("text")
                .attr("x", 20)
                .attr("y", 12)
                .text(d => d)
                .style("font-size", "12px")
                .attr('fill', '#fff')
        }
    }
}
</script>

<style lang="scss" scoped>
.d3-rect-2 {
    position: relative;

    .svg {
        position: relative;
        height: 100%;
        z-index: 9;
    }

}
</style>