<template>
    <div ref="rect" class="d3-rect">
        <Layout title="GDP" unit="" :ch="ch" :active="active">
            <div class="svg"></div>
        </Layout>
    </div>
</template>

<script>
import * as d3 from 'd3';
import Layout from './layout.vue'
import { linearGradient } from './utils'
import { mapGetters } from 'vuex';
export default {
    props: {
        active: {
            type: Boolean,
            default: false
        },
        eh: {
            type: Number,
            default: 100
        },
        inDelay: {
            type: Number,
            default: 0
        },
        outDelay: {
            type: Number,
            default: 0
        }
    },
    components: {
        Layout
    },
    data() {
        return {
            svg: null,
            xScale: null,
            yScale: null
        }
    },
    computed: {
        ...mapGetters(['mapData']),
        ch() {
            return this.eh - 56
        }
    },
    watch: {
        mapData(data) {
            const { rect } = data;
            console.log(rect)
            this.update(rect)
        }
    },
    mounted() {
        this.init()
    },
    methods: {
        init() {

            // 
            const width = 480, height = this.ch;
            const data = this.mapData.rect || [
                { label: "", value: 10 },
                { label: "", value: 20 },
                { label: "", value: 30 },
                { label: "", value: 40 },
                { label: "", value: 50 },
            ];

            // 
            const { top, right, bottom, left } = { top: 20, right: 10, bottom: 40, left: 40 }

            //  SVG 
            const svg = d3.select(".d3-rect .svg")
                .append("svg")
                .attr("width", width)
                .attr("height", height);


            // 
            const x = d3.scaleBand()
                .domain(data.map((d) => d.label)) // 
                .range([left, width - right])
                .padding(0.5)
                .round(true)

            const y = d3.scaleLinear()
                .domain([0, d3.max(data.map(({ value }) => value))]) // 
                .nice() // 
                .range([height - bottom, top]); // Y 

            // 
            linearGradient(svg, 'd3-rect-linearGradient', ['rgb(13% 41% 89%)', 'rgb(13% 72% 89%)', 'rgb(13% 99% 89%)'])

            svg.selectAll("rect")
                .data(data)
                .join("rect")
                .attr('class', 'd3-rect-item')
                .attr("x", (d, i) => x(d.label))
                .attr("y", d => y(0))
                .attr("width", x.bandwidth())
                .attr("height", 0)
                .attr("fill", "url(#d3-rect-linearGradient)") // 
                .transition()
                .duration(600) //  1000ms
                .delay((d, i) => i * 600 / data.length)
                .attr("y", d => y(d.value))
                .attr("height", d => y(0) - y(d.value))

            //  X 
            svg.append("g")
                .attr('class', 'x-axis')
                .attr("transform", `translate(0,${height - bottom + 5})`)
                .call(d3.axisBottom(x).tickFormat((a) => a).tickSize(0))
                .attr("font-size", '12px')
                .attr('color', '#fff')
                .attr("opacity", 0)
                .transition()
                .duration(1000) //  1000ms
                .attr("opacity", 1)

            svg.select(".x-axis")
                .selectAll(".domain")
                .style("display", "none");

            svg.select('.x-axis')
                .selectAll('text')
                .attr("transform", `translate(0,5)`)

            //  Y 
            svg.append("g")
                .attr('class', 'y-axis')
                .attr("transform", `translate(${left - 5},0)`)
                .call(d3.axisLeft(y).ticks(5).tickSize(0))
                .attr("font-size", '12px')
                .attr('color', '#fff')

            svg.select(".y-axis")
                .selectAll(".domain")
                .style("display", "none");
            // 
            this.svg = svg
            this.xScale = x;
            this.yScale = y;

        },
        update(data) {
            const height = this.ch;
            this.xScale.domain(data.map(d => d.label));
            this.yScale.domain([0, d3.max(data, d => d.value)]);

            // 
            this.svg.select(".x-axis").call(d3.axisBottom(this.xScale).tickFormat((a) => a).tickSize(0));
            this.svg.select(".y-axis").call(d3.axisLeft(this.yScale).ticks(5).tickSize(0));

            // 
            const bars = this.svg.selectAll(".d3-rect-item").data(data, d => d.label);

            // 
            bars
                .attr("x", d => this.xScale(d.label))
                .attr("y", d => this.yScale(d.value))
                .attr("width", this.xScale.bandwidth())
                .attr("height", d => height - this.yScale(d.value));

            // 
            bars
                .enter()
                .append("rect")
                .attr('class', 'd3-rect-item')
                .attr("x", (d, i) => this.xScale(d.label))
                .attr("y", d => this.yScale(0))
                .attr("width", this.xScale.bandwidth())
                .attr("height", 0)
                .attr("fill", "url(#d3-rect-linearGradient)") // 
                .transition()
                .duration(600) //  1000ms
                .delay((d, i) => i * 600 / data.length)
                .attr("y", d => this.yScale(d.value))
                .attr("height", d => this.yScale(0) - this.yScale(d.value))

            // 
            bars
                .exit()
                .transition()
                .duration(500)
                .attr("y", 0)
                .attr("height", 0)
                .remove();
        }
    }
}
</script>

<style lang="scss" scoped>
.d3-rect {
    position: relative;

    .svg {
        position: relative;
        height: 100%;
        z-index: 9;
    }

}
</style>