<template>
    <div ref="line" class="d3-pie">
        <Layout title="" unit="" :ch="ch" :active="active">
            <div class="svg"></div>
        </Layout>
    </div>
</template>

<script>
import * as d3 from 'd3';
import Layout from './layout.vue'
import { linearGradient } from './utils'
import { mapGetters } from 'vuex';
export default {
    props: {
        active: {
            type: Boolean,
            default: false
        },
        eh: {
            type: Number,
            default: 100
        },
        inDelay: {
            type: Number,
            default: 0
        },
        outDelay: {
            type: Number,
            default: 0
        }
    },
    components: {
        Layout
    },
    data() {
        return {
            svg: null,
            pie: null,
        }
    },
    computed: {
        ...mapGetters(['mapData']),
        ch() {
            return this.eh - 56
        }
    },
    watch: {
        mapData(data) {
            const { pie } = data;
            // this.update(pie)
        }
    },
    mounted() {
        this.init()
    },
    methods: {
        init() {
            const width = 480 - 40, height = this.ch - 40;
            // 
            const data = [
                { label: "", value: 36, radius: 0.8, color: ['rgb(73% 38% 64%)', 'rgb(73% 61% 64%)'], },
                { label: "", value: 34, radius: 0.9, color: ['rgb(13% 41% 89%)', 'rgb(13% 66% 89%)'], },
                { label: "", value: 40, radius: 1, color: ['rgb(60% 12% 26%)', 'rgb(100% 28% 48%)'], }
            ];
            // const { value: max } = data.reduce((x, y) => ({ value: x.value + y.value }), { value: 0 })
            //  SVG 
            const { top, right, bottom, left } = { top: 20, right: 20, bottom: 20, left: 20 }

            const svg = d3.select(".d3-pie .svg")
                .append("svg")
                .attr("width", width)
                .attr("height", height)
                .attr("transform", `translate(${left},${top})`)
                .append("g")
                .attr("transform", `translate(${width / 2 - 100}, ${height / 2})`)


            const radius = Math.min(width, height) / 2;

            // 
            const color = d3
                .scaleOrdinal()
                .domain(data.map(d => d.label))
                .range(d3.schemeCategory10);

            // 
            const pie = d3.pie().value(d => d.value);

            // 
            const arc = d3
                .arc()
                .innerRadius(0) //  0  0 
                .outerRadius(0);
            const arcExpanded = d3
                .arc()
                .innerRadius(0)
                .outerRadius((d, i) => d.data.radius * radius);
            // 
            const defs = svg.append("defs");
            const filter = defs.append("filter")
                .attr("id", "d3-pir-shadow")
                .attr("x", "-50%")
                .attr("y", "-50%")
                .attr("width", "200%")
                .attr("height", "200%");
            filter.append("feGaussianBlur")
                .attr("in", "SourceAlpha")
                .attr("stdDeviation", 4)
                .attr("result", "blur");
            filter.append("feOffset")
                .attr("in", "blur")
                .attr("dx", 2)
                .attr("dy", 2)
                .attr("result", "offsetBlur");
            const feMerge = filter.append("feMerge");
            feMerge.append("feMergeNode").attr("in", "offsetBlur");
            feMerge.append("feMergeNode").attr("in", "SourceGraphic");
            // 
            data.map((el, i) => {
                const id = `d3-pie-${i}`
                linearGradient(svg, id, el.color)
            })

            // 
            svg
                .selectAll("path")
                .data(pie(data))
                .enter()
                .append("path")
                .attr('class', 'd3-pie-item')
                .attr("d", arc)
                .attr("fill", (d, i) => `url(#d3-pie-${i})`)
                .attr("filter", 'url(#d3-pir-shadow)')
                .attr('opacity', '0')
                .transition() // 
                .duration(1000) // 
                .delay((d, i) => i * 200)
                .ease(d3.easeExp)
                .attr('opacity', '1')
                .attrTween("d", function (d) {
                    const interpolate = d3.interpolate(arc(d), arcExpanded(d)); // 
                    return function (t) {
                        return interpolate(t); //  t 
                    };
                });

            data.map((d, i) => {
                svg.append("text")
                    .attr("x", 200)
                    .attr("y", (i - 1) * 50)
                    .attr("text-anchor", "middle")
                    .style("font-size", "16px")
                    .style("fill", `url(#d3-pie-${i})`)
                    .text(`${d.label} ${d.value}%`)
                    .attr("opacity", `0`)
                    .attr("transform", `translate(40,0)`)
                    .transition()
                    .duration(1000)
                    .delay(i * 200)
                    .attr("opacity", `1`)
                    .attr("transform", `translate(0,0)`)
                    .tween("text", function () {
                        const interpolate = d3.interpolateNumber(0, d.value);
                        return function (t) {
                            this.textContent = d.label + '  ' + interpolate(t).toFixed(2) + '%';
                        };
                    });
            })
            this.svg = svg
            this.pie = pie
        },
        update(d) {
            const data = [
                { color: ['rgb(73% 38% 64%)', 'rgb(73% 61% 64%)'], },
                { color: ['rgb(13% 41% 89%)', 'rgb(13% 66% 89%)'], },
                { color: ['rgb(60% 12% 26%)', 'rgb(100% 28% 48%)'], }
            ].map((el, i) => {
                return {
                    ...el,
                    ...d[i]
                }
            })
            // 
            const arcs = svg.selectAll(".d3-pie-item").data(this.pie(data), d => d.data.label);
            const arc = d3
                .arc()
                .innerRadius(0) //  0  0 
                .outerRadius(0)
            const arcExpanded = d3
                .arc()
                .innerRadius(0)
                .outerRadius((d, i) => d.data.radius * radius);
            // 
            arcs
                .select("path")
                .transition()
                .duration(500)
                .attrTween("d", function (d) {
                    const interpolate = d3.interpolate(arc(d), arcExpanded(d)); // 
                    return function (t) {
                        return interpolate(t); //  t 
                    };
                });


            // 
            const newArcs = arcs
                .enter()
                .append("g")
                .attr("class", "d3-pie-item");

            newArcs
                .append("path")
                .attr("fill", (d, i) => `url(#d3-pie-${i})`)
                .each(function (d) {
                    this._current = d;
                }) // 
                .transition()
                .duration(500)
                .attrTween("d", function (d) {
                    const i = d3.interpolate(this._current, d);
                    this._current = i(1);
                    return t => arc(i(t));
                });

            newArcs
                .append("text")
                .attr("transform", d => `translate(${arc.centroid(d)})`)
                .text(d => d.data.label)
                .style("fill-opacity", 0)
                .transition()
                .duration(500)
                .style("fill-opacity", 1);

            // 
            arcs
                .exit()
                .select("path")
                .transition()
                .duration(500)
                .attrTween("d", function (d) {
                    const i = d3.interpolate(this._current, { startAngle: d.startAngle, endAngle: d.startAngle });
                    return t => arc(i(t));
                })
                .remove();

            arcs.exit().select("text").remove();
        }
    }
}
</script>

<style lang="scss" scoped>
.d3-pie {
    position: relative;

    .svg {
        position: relative;
        height: 100%;
        z-index: 9;
    }

}
</style>