<template>
    <div class="d3">

        <div ref="d3Box" class="d3-box">
            <Background />
            <Map class="m-map" />
            <Rect class="l l1" :eh="300" />
            <Line class="l l2" :eh="300" />
            <Pie class="l l3" :eh="300" />
            <Progress class="r r1" :eh="300" />
            <Rect2 class="r r2" :eh="300" />
            <Dimension class="r r3" :eh="300" />
        </div>
    </div>
</template>

<script>
// import * as THREE from 'three';
// import { OrbitControls } from 'three/addons/controls/OrbitControls.js';
// import { SVGLoader } from 'three/examples/jsm/loaders/SVGLoader.js';

import Background from './back.vue'
import Map from './components/map.vue'
import Rect from './components/rect.vue'
import Line from './components/line.vue'
import Pie from './components/pie.vue'
import Progress from './components/progress.vue'
import Rect2 from './components/rect2.vue'
import Dimension from './components/dimension.vue'
// import Globe from 'globe.gl';
export default {
    components: {
        Background,
        Map,
        Rect,
        Line,
        Pie,
        Progress,
        Rect2,
        Dimension
    },

    mounted() {
        const w = window.innerWidth
        this.$refs.d3Box.style.transform = `matrix(${w / 1920},0,0,${w / 1920},0,0)`;

    },
    methods: {
    }
}
</script>

<style lang="scss" scoped>
.d3 {
    position: relative;
    height: 100%;
    background-color: #000;

    .d3-box {
        position: absolute;
        width: 1920px;
        height: 1080px;
        position: absolute;
        top: calc(50% - 540px);
        left: calc(50% - 960px);
        overflow: hidden;
        // background-color: rgba(0, 0, 0, .1);

        .m-map {
            position: absolute;
            top: 100px;
            left: 50%;
            transform: translate(-50%, 0);
        }

        .l {
            position: absolute;
            left: 0;

            &.l1 {
                top: 100px;
            }

            &.l2 {
                top: 420px;
            }

            &.l3 {
                top: 740px;
            }
        }

        .r {
            position: absolute;
            right: 0;

            &.r1 {
                top: 100px;
            }

            &.r2 {
                top: 420px;
            }

            &.r3 {
                top: 740px;
            }
        }
    }

}
</style>