<template>
    <div :class="['d3-layout', active ? 'active' : '']">
        <div v-if="title" ref="content" class="title">
            <svg class="b" :width="cw" :height="36" :viewBox="viewBox" version="1.1">
                <defs>
                    <filter id="glow">
                        <!--  -->
                        <feGaussianBlur in="SourceGraphic" stdDeviation="1" result="blur" />
                        <!--  -->
                        <feFlood flood-color="#fff" flood-opacity="1" result="color" />
                        <feComposite in="color" in2="blur" operator="in" result="glow" />
                        <!--  -->
                        <feMerge>
                            <feMergeNode in="glow" />
                            <feMergeNode in="SourceGraphic" />
                        </feMerge>
                    </filter>
                </defs>
                <rect class="line" x="0" y="0" :width="cw" height="36" fill="transparent" stroke="#5596ff"
                    stroke-width="2" stroke-dasharray="0 1032" stroke-dashoffset="0" filter="url(#glow)"
                    :style="{ '--total': `${total} ${total}` }" />
            </svg>
            <div class="word">
                <div class="t">{{ title }}</div>
                <div class="u">{{ unit }}</div>
            </div>
        </div>
        <div ref="box" class="box" :style="{ height: `${ch}px` }">
            <svg class="b" :width="cw" :height="ch" version="1.1">
                <polyline class="polyline" :points="polylinePath" fill="transparent" stroke="rgb(10% 100% 100%)"
                    stroke-width="2" :stroke-dasharray="`0 ${cw}`" stroke-dashoffset="0" />
            </svg>
            <slot></slot>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        title: {
            type: String,
            default: ""
        },
        unit: {
            type: String,
            default: ""
        },
        ch: {
            type: Number,
            default: 100
        },
        active: {
            type: Boolean,
            default: false
        },
    },
    data() {
        return {
            cw: 0,
            viewBox: '0 0 100 36',
            total: '',
            bviewBox: '',
            polylinePath: ""
        }
    },
    mounted() {
        if (this.title) {
            const { clientWidth } = this.$refs.content;
            this.cw = clientWidth
            this.viewBox = `0 0 ${clientWidth} 36`;
            this.total = (clientWidth * 2 + 36 * 2)
        }
        const { clientWidth, clientHeight } = this.$refs.box;
        this.bviewBox = `0 0 ${clientWidth} ${clientHeight}`
        this.polylinePath = `0,${clientHeight} ${clientWidth},${clientHeight}`
    },
    methods: {
    }
}
</script>

<style lang="scss" scoped>
.d3-layout {
    width: 480px;

    .title {
        position: relative;
        background: linear-gradient(90deg, rgba(85, 150, 255, .1), rgba(0, 0, 0, .05), rgba(85, 150, 255, .1));
        margin-bottom: 20px;

        .word {
            position: relative;
            height: 36px;
            line-height: 36px;
            padding: 0 18px;
            font-size: 15px;
            color: #fefefe;
            letter-spacing: 2px;
            display: flex;
            justify-content: space-between;
            z-index: 2;

            .u {
                flex-grow: 0;
                flex-shrink: 0;
            }
        }

        .b {
            position: absolute;
            top: 0;
            left: 0;
            pointer-events: none;

            .line {
                animation: identifier 1600ms ease-in-out forwards;

                @keyframes identifier {

                    // 70% {
                    //     stroke-dasharray: 516 516;
                    //     // stroke-dashoffset: -516
                    // }

                    100% {
                        stroke-dasharray: var(--total);
                        // stroke-dashoffset: -1032
                    }
                }
            }

        }
    }

    .box {
        position: relative;
        background: linear-gradient(90deg, rgba(10, 46, 103, .2), rgba(10, 46, 103, 0), rgba(10, 46, 103, .2));

        .b {
            position: absolute;
            top: 0;
            left: 0;
            pointer-events: none;

            .polyline {
                animation: identifier2 3000ms linear infinite;

                @keyframes identifier2 {

                    30% {
                        stroke-dasharray: 120 360;
                        stroke-dashoffset: -180
                    }

                    60% {
                        stroke-dasharray: 0 480;
                        stroke-dashoffset: -480
                    }
                }
            }
        }

        // background: rgba(255, 255, 255, .3);

    }

    &.active {}
}
</style>