<template>
    <div ref="map" class="map" :style="{ width: width + 'px', height: height + 'px' }">
        <div class="svg"></div>
        <!-- <Filings /> -->
        <v-snackbar v-model="alert" location="top" color="primary" timeout="2000">
            
        </v-snackbar>
    </div>
</template>

<script>
import * as d3 from 'd3';
import { mapActions, mapGetters } from 'vuex';
export default {
    props: {
        width: {
            type: Number,
            default: 900
        },
        height: {
            type: Number,
            default: 620
        }
    },
    data() {
        return {
            mapData: [],
            alert: false
        }
    },
    mounted() {
        this.mapData = {
            320000: {
                rect: [
                    { label: "", value: 18 },
                    { label: "", value: 21 },
                    { label: "", value: 12 },
                    { label: "", value: 11 },
                    { label: "", value: 10 },
                ],
                line: [
                    [
                        { x: 2018, y: 1.5 },
                        { x: 2019, y: 1.9 },
                        { x: 2020, y: 1.7 },
                        { x: 2021, y: 2.0 },
                        { x: 2022, y: 2.3 },
                        { x: 2023, y: 2.6 },
                        { x: 2024, y: 3.0 }
                    ], [
                        { x: 2018, y: 3.5 },
                        { x: 2019, y: 3.9 },
                        { x: 2020, y: 4.1 },
                        { x: 2021, y: 3.8 },
                        { x: 2022, y: 4.0 },
                        { x: 2023, y: 3.9 },
                        { x: 2024, y: 4.3 }
                    ], [
                        { x: 2018, y: 4.3 },
                        { x: 2019, y: 4.5 },
                        { x: 2020, y: 4.3 },
                        { x: 2021, y: 4.7 },
                        { x: 2022, y: 4.7 },
                        { x: 2023, y: 4.9 },
                        { x: 2024, y: 4.8 }
                    ]
                ],
                pie: [
                    { label: "", value: 46, radius: 0.8 },
                    { label: "", value: 34, radius: 0.9 },
                    { label: "", value: 30, radius: 1 }
                ],
            },
            330000: {
                rect: [
                    { label: "", value: 19 },
                    { label: "", value: 11 },
                    { label: "", value: 8 },
                    { label: "", value: 7 },
                    { label: "", value: 5 },
                ],
                line: [
                    [
                        { x: 2018, y: 1.2 },
                        { x: 2019, y: 1.3 },
                        { x: 2020, y: 1.5 },
                        { x: 2021, y: 1.6 },
                        { x: 2022, y: 2.0 },
                        { x: 2023, y: 2.1 },
                        { x: 2024, y: 2.2 }
                    ], [
                        { x: 2018, y: 2.3 },
                        { x: 2019, y: 2.7 },
                        { x: 2020, y: 2.4 },
                        { x: 2021, y: 2.6 },
                        { x: 2022, y: 2.8 },
                        { x: 2023, y: 2.9 },
                        { x: 2024, y: 3.1 }
                    ], [
                        { x: 2018, y: 3.1 },
                        { x: 2019, y: 3.3 },
                        { x: 2020, y: 3.2 },
                        { x: 2021, y: 3.5 },
                        { x: 2022, y: 3.5 },
                        { x: 2023, y: 3.7 },
                        { x: 2024, y: 3.6 }
                    ]
                ],
                pie: [
                    { label: "", value: 20, radius: 0.8 },
                    { label: "", value: 54, radius: 0.9 },
                    { label: "", value: 26, radius: 1 }
                ],
            },
            'qg': {
                rect: [
                    { label: "", value: 10 },
                    { label: "", value: 20 },
                    { label: "", value: 30 },
                    { label: "", value: 40 },
                    { label: "", value: 50 },
                ],
                line: [
                    [
                        { x: 2018, y: 1.3 },
                        { x: 2019, y: 1.7 },
                        { x: 2020, y: 1.5 },
                        { x: 2021, y: 1.8 },
                        { x: 2022, y: 2.1 },
                        { x: 2023, y: 2.4 },
                        { x: 2024, y: 2.8 }
                    ], [
                        { x: 2018, y: 3.3 },
                        { x: 2019, y: 3.7 },
                        { x: 2020, y: 3.4 },
                        { x: 2021, y: 3.6 },
                        { x: 2022, y: 3.8 },
                        { x: 2023, y: 3.9 },
                        { x: 2024, y: 4.1 }
                    ], [
                        { x: 2018, y: 4.1 },
                        { x: 2019, y: 4.3 },
                        { x: 2020, y: 4.2 },
                        { x: 2021, y: 4.5 },
                        { x: 2022, y: 4.5 },
                        { x: 2023, y: 4.7 },
                        { x: 2024, y: 4.6 }
                    ]
                ],
                pie: [
                    { label: "", value: 36, radius: 0.8 },
                    { label: "", value: 34, radius: 0.9 },
                    { label: "", value: 40, radius: 1 }
                ],
            }
        }
        this.init()
    },
    methods: {
        ...mapActions(['setMapData']),
        init() {
            // 
            const width = this.width, height = this.height;

            //  SVG 
            const svg = d3.select(".map .svg").append('svg').attr('width', width).attr('height', height);
            const group = svg.append('g')

            const tooltip = d3
                .select("body")
                .append("div")
                .attr("class", "tooltip")
                .style("opacity", 0);
            // 
            const projection = d3.geoMercator()
                .center([104, 35]) // 
                .scale(600) // 
                .translate([width / 2, height / 2]); // 

            // 
            const path = d3.geoPath().projection(projection);
            // 
            const defs = svg.append("defs");
            const filter = defs.append("filter")
                .attr("id", "shadow")
                .attr("x", "-50%")
                .attr("y", "-50%")
                .attr("width", "200%")
                .attr("height", "200%");
            filter.append("feGaussianBlur")
                .attr("in", "SourceAlpha")
                .attr("stdDeviation", 2)
                .attr("result", "blur");
            filter.append("feOffset")
                .attr("in", "blur")
                .attr("dx", 2)
                .attr("dy", 2)
                .attr("result", "offsetBlur");
            const feMerge = filter.append("feMerge");
            feMerge.append("feMergeNode").attr("in", "offsetBlur");
            feMerge.append("feMergeNode").attr("in", "SourceGraphic");
            const b = window.innerWidth / 1920
            //  GeoJSON 
            d3.json('https://geojson.cn/api/china/china.json').then((geoData) => {
                // const countries = topojson.feature(geoData, geoData.objects.default);
                // 
                this.setMapData(this.mapData['qg'])
                const i = group.selectAll("path")
                    .data(geoData.features)
                    .enter()
                    .append("path")
                    .attr("d", path)
                    .attr("fill", (d) => {

                        return [320000, 330000].includes(d.properties.code) ? "rgba(54,127,207,.5)" : "rgba(54,127,207,.1)"
                    }) // 
                    .attr("stroke", "rgba(255,255,255,1)") // 
                    .attr("stroke-width", 1)
                    // .attr('stroke-dasharray', '50 50')
                    // .attr("filter", "url(#shadow)")
                    .attr('opacity', 0)
                    .on("mouseover", function (e, d) {
                        d3.select(this).attr('cursor', 'pointer').transition().attr("fill", "rgba(54,127,207,1)"); // 
                        tooltip
                            .style("opacity", 1)
                            .html(`: ${d.properties.name}`);

                    })
                    .on("mousemove", function (event) {
                        tooltip
                            .style("left", `${event.pageX - 10}px`)
                            .style("top", `${event.pageY - 30}px`);
                    })
                    .on("mouseout", function (e, d) {
                        d3.select(this).attr('cursor', 'none').transition().attr("fill", [320000, 330000].includes(d.properties.code) ? "rgba(54,127,207,.5)" : "rgba(54,127,207,.1)");
                        tooltip.style("opacity", 0);
                    })
                    .on("click", (e, d) => {
                        const { code } = d.properties;
                        const data = this.mapData[code]
                        if (data) {
                            this.setMapData(data)
                        } else {
                            this.alert = true
                        }
                    });
                i.transition()
                    .duration(1000) //  1000ms
                    .delay((d, i) => i * 10) //  200ms
                    .attr("opacity", 1) //  y 
            }).catch((error) => {
                console.error("Error loading GeoJSON data:", error);
            });



        }
    }
}
</script>

<style lang="scss" scoped>
.map {
    position: relative;

    .svg {
        position: relative;
        height: 100%;
        z-index: 9;
    }

}
</style>