<template>
    <div ref="line" class="d3-line">
        <Layout title="" unit="" :ch="ch" :active="active">
            <div class="svg"></div>
        </Layout>
    </div>
</template>

<script>
import * as d3 from 'd3';
import Layout from './layout.vue'
import { linearGradient } from './utils'
import { mapGetters } from 'vuex';
export default {
    props: {
        active: {
            type: Boolean,
            default: false
        },
        eh: {
            type: Number,
            default: 100
        },
        inDelay: {
            type: Number,
            default: 0
        },
        outDelay: {
            type: Number,
            default: 0
        }
    },
    components: {
        Layout
    },
    data() {
        return {
            svg: null,
            xScale: null,
            yScale: null
        }
    },
    computed: {
        ...mapGetters(['mapData']),
        ch() {
            return this.eh - 56
        }
    },
    watch: {
        mapData(data) {
            const { line } = data;
            this.update(line)
        }
    },
    mounted() {
        this.init()
    },
    methods: {
        init() {
            const width = 480, height = this.ch;
            // 
            const data = [
                {
                    name: "",
                    color: ['#fc8aff', '#fcd6ff'],
                    values: [
                        { x: 2018, y: 1.3 },
                        { x: 2019, y: 1.7 },
                        { x: 2020, y: 1.5 },
                        { x: 2021, y: 1.8 },
                        { x: 2022, y: 2.1 },
                        { x: 2023, y: 2.4 },
                        { x: 2024, y: 2.8 }
                    ]
                },
                {
                    name: "",
                    color: ['#1f73ff', '#1fc9ff'],
                    values: [
                        { x: 2018, y: 3.3 },
                        { x: 2019, y: 3.7 },
                        { x: 2020, y: 3.4 },
                        { x: 2021, y: 3.6 },
                        { x: 2022, y: 3.8 },
                        { x: 2023, y: 3.9 },
                        { x: 2024, y: 4.1 }
                    ]
                },
                {
                    name: "",
                    color: ['#eb3d47', '#eb8c1c'],
                    values: [
                        { x: 2018, y: 4.1 },
                        { x: 2019, y: 4.3 },
                        { x: 2020, y: 4.2 },
                        { x: 2021, y: 4.5 },
                        { x: 2022, y: 4.5 },
                        { x: 2023, y: 4.7 },
                        { x: 2024, y: 4.6 }
                    ]
                }
            ];

            //  SVG 
            const { top, right, bottom, left } = { top: 20, right: 20, bottom: 40, left: 40 }

            const svg = d3.select(".d3-line .svg")
                .append("svg")
                .attr("width", width)
                .attr("height", height);

            // 
            const xScale = d3.scaleBand()
                .domain(data[0].values.map((d) => d.x)) // 
                .range([left - 20, width])
                .padding(1)
                .round(true)

            const yScale = d3
                .scaleLinear()
                .domain([0, Math.ceil(d3.max(data, (series) => d3.max(series.values, (d) => d.y)))])
                .range([height - bottom, top]);


            //  X 
            svg.append("g")
                .attr('class', 'x-axis')
                .attr("transform", `translate(0,${height - bottom + 5})`)
                .call(d3.axisBottom(xScale).tickFormat((a) => a).tickSize(0).ticks(4))
                .attr("font-size", '12px')
                .attr('color', '#fff')
                .attr("opacity", 0)
                .transition()
                .duration(1000) //  1000ms
                .attr("opacity", 1)

            svg.select(".x-axis")
                .selectAll(".domain")
                .style("display", "none");

            svg.select('.x-axis')
                .selectAll('text')
                .attr("transform", `translate(0,5)`)

            //  Y 
            svg.append("g")
                .attr('class', 'y-axis')
                .attr("transform", `translate(${left},0)`)
                .call(d3.axisLeft(yScale).ticks(5).tickSize(0).tickFormat((d) => `${d} %`))
                .attr("font-size", '12px')
                .attr('color', '#fff')


            svg.select(".y-axis")
                .selectAll(".domain")
                .style("display", "none");

            // 
            const line = d3
                .line()
                .x((d) => xScale(d.x))
                .y((d) => yScale(d.y))
                .curve(d3.curveMonotoneX);

            // 
            data.forEach((series, i) => {
                const id = `d3-line-${i}`
                linearGradient(svg, id, series.color)
                svg.append("path")
                    .datum(series.values)
                    .attr("class", "d3-line-path")
                    .attr("d", line)
                    .attr('fill', 'transparent')
                    .attr("stroke-width", 2)
                    .attr("stroke", `url(#${id})`)
                    .attr("stroke-dasharray", function () {
                        const length = this.getTotalLength();
                        return `${length} ${length}`;
                    })
                    .attr("stroke-dashoffset", function () {
                        return this.getTotalLength();
                    }).transition() // 
                    .duration(1200) // 
                    .delay(i * 600 / data.length)
                    .ease(d3.easeCircle) // 
                    .attr("stroke-dashoffset", 0); //  0

                // 
                svg.selectAll(`.d3-line-text-label${i}`)
                    .data(series.values)
                    .enter()
                    .append("text")
                    .attr("class", "text-label")
                    .attr("x", (d) => xScale(d.x) - xScale.bandwidth() / 2) //  x 
                    .attr("y", (d) => yScale(d.y) - 8) //  y 
                    .attr("font-size", '12px')
                    .attr('fill', series.color[0])
                    .text((d) => d.y) //  y 
                    .attr("opacity", 0)
                    .transition()
                    .duration(1000) //  1000ms
                    .ease(d3.easeCircle)
                    .delay((d, j) => i * 600 / data.length + j * (1200 / series.values.length))
                    .attr("opacity", 1)
                // 
                svg.selectAll(`.d3-line-dot${i}`)
                    .data(series.values)
                    .enter()
                    .append("circle")
                    .attr("class", "dot")
                    .attr("cx", (d) => xScale(d.x) + xScale.bandwidth() / 2) // x
                    .attr("cy", (d) => yScale(d.y)) // y
                    .attr("r", 3)
                    .attr('fill', '#fff')
                    .attr("opacity", 0)
                    .transition()
                    .duration(1000) //  1000ms
                    .ease(d3.easeCircle)
                    .delay((d, j) => i * 600 / data.length + j * (1200 / series.values.length))
                    .attr("opacity", 1)
            });
            this.svg = svg
            this.xScale = xScale;
            this.yScale = yScale;
        },
        update(d) {
            const data = [
                {
                    name: "",
                    color: ['#fc8aff', '#fcd6ff'],
                },
                {
                    name: "",
                    color: ['#1f73ff', '#1fc9ff'],
                },
                {
                    name: "",
                    color: ['#eb3d47', '#eb8c1c'],
                }
            ].map((el, i) => {
                return {
                    ...el,
                    values: d[i]
                }
            });
            console.log(data)
            const height = this.ch;
            this.xScale.domain(data[0].values.map((d) => d.x));
            this.yScale.domain([0, Math.ceil(d3.max(data, (series) => d3.max(series.values, (d) => d.y)))]);

            // 
            this.svg.select(".x-axis").call(d3.axisBottom(this.xScale).tickFormat((a) => a).tickSize(0).ticks(4));
            this.svg.select(".y-axis").call(d3.axisLeft(this.yScale).ticks(5).tickSize(0).tickFormat((d) => `${d} %`));

            // 


            // 
            // bars
            //     .enter()
            // 
            this.svg.selectAll(".d3-line-path")
                .transition()
                .duration(500)
                .attr("stroke-dashoffset", function () {
                    return -this.getTotalLength();
                })
                .remove();

            this.svg.selectAll(".text-label")
                .transition()
                .duration(500)
                .attr("opacity", 0)
                .remove();

            this.svg.selectAll(".dot")
                .transition()
                .duration(500)
                .attr("opacity", 0)
                .remove();
            // 
            const line = d3
                .line()
                .x((d) => this.xScale(d.x))
                .y((d) => this.yScale(d.y))
                .curve(d3.curveMonotoneX);

            // 
            data.forEach((series, i) => {
                const id = `d3-line-${i}`
                linearGradient(this.svg, id, series.color)
                this.svg.append("path")
                    .datum(series.values)
                    .attr("class", "d3-line-path")
                    .attr("d", line)
                    .attr('fill', 'transparent')
                    .attr("stroke-width", 2)
                    .attr("stroke", `url(#${id})`)
                    .attr("stroke-dasharray", function () {
                        const length = this.getTotalLength();
                        return `${length} ${length}`;
                    })
                    .attr("stroke-dashoffset", function () {
                        return this.getTotalLength();
                    }).transition() // 
                    .duration(1200) // 
                    .delay(i * 600 / data.length)
                    .ease(d3.easeCircle) // 
                    .attr("stroke-dashoffset", 0); //  0

                // 
                this.svg.selectAll(`.d3-line-text-label${i}`)
                    .data(series.values)
                    .enter()
                    .append("text")
                    .attr("class", "text-label")
                    .attr("x", (d) => this.xScale(d.x) - this.xScale.bandwidth() / 2) //  x 
                    .attr("y", (d) => this.yScale(d.y) - 8) //  y 
                    .attr("font-size", '12px')
                    .attr('fill', series.color[0])
                    .text((d) => d.y) //  y 
                    .attr("opacity", 0)
                    .transition()
                    .duration(1000) //  1000ms
                    .ease(d3.easeCircle)
                    .delay((d, j) => i * 600 / data.length + j * (1200 / series.values.length))
                    .attr("opacity", 1)
                // 
                this.svg.selectAll(`.d3-line-dot${i}`)
                    .data(series.values)
                    .enter()
                    .append("circle")
                    .attr("class", "dot")
                    .attr("cx", (d) => this.xScale(d.x) + this.xScale.bandwidth() / 2) // x
                    .attr("cy", (d) => this.yScale(d.y)) // y
                    .attr("r", 3)
                    .attr('fill', '#fff')
                    .attr("opacity", 0)
                    .transition()
                    .duration(1000) //  1000ms
                    .ease(d3.easeCircle)
                    .delay((d, j) => i * 600 / data.length + j * (1200 / series.values.length))
                    .attr("opacity", 1)
            });


        }
    }
}
</script>

<style lang="scss" scoped>
.d3-line {
    position: relative;

    .svg {
        position: relative;
        height: 100%;
        z-index: 9;
    }

}
</style>