<template>
    <div ref="dimension" class="d3-dimension">
        <Layout title="" unit="" :ch="ch" :active="active">
            <div class="svg"></div>
        </Layout>
    </div>
</template>

<script>
import * as d3 from 'd3';
import Layout from './layout.vue'
import { linearGradient } from './utils'
export default {
    props: {
        active: {
            type: Boolean,
            default: false
        },
        eh: {
            type: Number,
            default: 100
        },
        inDelay: {
            type: Number,
            default: 0
        },
        outDelay: {
            type: Number,
            default: 0
        }
    },
    components: {
        Layout
    },
    computed: {
        ch() {
            return this.eh - 56
        }
    },
    mounted() {
        this.init()
    },
    methods: {
        init() {
            // 
            const data = [
            ];
            const colors = [
                { color: ['rgb(73% 38% 64%)', 'rgb(73% 61% 64%)'] },
                { color: ['rgb(13% 41% 89%)', 'rgb(13% 66% 89%)'] },
                { color: ['rgb(60% 12% 26%)', 'rgb(100% 28% 48%)'] }
            ]


            // 
            const margin = { top: 30, right: 30, bottom: 30, left: 40 };
            const width = 480 - margin.left - margin.right;
            const height = this.ch - margin.top - margin.bottom;

            //  SVG 
            const svg = d3
                .select(".d3-dimension .svg")
                .append("svg")
                .attr("width", width + margin.left + margin.right)
                .attr("height", height + margin.top + margin.bottom)
                .append("g")
                .attr("transform", `translate(${margin.left}, ${margin.top})`);

            colors.map((el, i) => {
                const id = `d3-dimension-${i}`
                linearGradient(svg, id, el.color)
            })
            for (let i = 0; i < 200; i++) {
                const y = Math.ceil(Math.random() * 80);
                const v = Math.ceil(Math.random() * 80);
                const ci = v < 30 ? 0 : v < 60 ? 1 : 2;
                data.push({ x: Math.ceil(Math.random() * 75), y, v, color: `url(#d3-dimension-${ci})` })
            }
            // 
            const xScale = d3
                .scaleLinear()
                .domain([0, d3.max(data, d => d.x)])
                .range([0, width]);

            const yScale = d3
                .scaleLinear()
                .domain([0, d3.max(data, d => d.y) + 5])
                .range([height - 15, 0]);

            // 
            // const xAxis = d3.axisBottom(xScale);
            // const yAxis = d3.axisLeft(yScale);

            //  X 
            // svg
            //     .append("g")
            //     .attr("transform", `translate(0, ${height})`)
            //     .call(xAxis);
            svg.append("g")
                .attr('class', 'x-axis')
                .attr("transform", `translate(0,${height - 10})`)
                .call(d3.axisBottom(xScale).tickFormat((a) => a).tickSize(0))
                .attr("font-size", '12px')
                .attr('color', '#fff')
            svg.select(".x-axis")
                .selectAll(".domain")
                .style("display", "none");

            svg.select('.x-axis')
                .selectAll('text')
                .attr("transform", `translate(0,10)`)
            //  Y 
            svg.append("g")
                .attr('class', 'y-axis')
                .attr("transform", `translate(-10,0)`)
                .call(d3.axisLeft(yScale).ticks(5).tickSize(0))
                .attr("font-size", '12px')
                .attr('color', '#fff')

            svg.select(".y-axis")
                .selectAll(".domain")
                .style("display", "none");

            // 
            svg
                .selectAll(".d3-dimension-circle")
                .data(data)
                .enter()
                .append("circle")
                .attr("class", 'd3-dimension-circle')
                .attr("cx", d => xScale(d.x))
                .attr("cy", d => yScale(d.y))
                .attr("r", d => d.v / 40 * 5)
                .attr("fill", (d, i) => d.color)
                .attr("opacity", 0)
                .attr('transform-origin', '50% 50%')
                .attr("transform", `matrix(1,0,0,1,0,30)`)
                .transition()
                .duration(1000) //  1000ms
                .delay((d, i) => i * 10)
                .attr("opacity", 0.7)
                .attr("transform", `matrix(1,0,0,1,0,0)`)

            //  tooltip
            // const tooltip = d3
            //     .select("body")
            //     .append("div")
            //     .attr("class", "tooltip")
            //     .style("opacity", 0);

            //  tooltip
            svg
                .selectAll("circle")
                .on("mouseover", function (event, d) {
                    d3.select(this).attr("opacity", "1");
                    // tooltip
                    //     .style("opacity", 1)
                    //     .html(`x: ${d.x}<br>y: ${d.y}`)
                    //     .style("left", `${event.pageX + 10}px`)
                    //     .style("top", `${event.pageY - 10}px`);
                })
                .on("mouseout", function () {
                    d3.select(this).attr("opacity", "0.7");
                    // tooltip.style("opacity", 0);
                });

            // 
            // svg
            //     .append("text")
            //     .attr("text-anchor", "middle")
            //     .attr("x", width / 2)
            //     .attr("y", height + margin.bottom - 5)
            //     .text("X  ()");

            // svg
            //     .append("text")
            //     .attr("text-anchor", "middle")
            //     .attr("transform", `rotate(-90)`)
            //     .attr("x", -height / 2)
            //     .attr("y", -margin.left + 15)
            //     .text("Y  ()");
        }
    }
}
</script>

<style lang="scss" scoped>
.d3-dimension {
    position: relative;

    .svg {
        position: relative;
        height: 100%;
        z-index: 9;
        // transform-origin: ;
    }

}
</style>